<template>
  <div class="v">
    <el-container>
        <div>{{aa}}</div>
        <el-header>
          <el-button plain @click="to_xiaonini">朴素按钮</el-button>
        </el-header>
        <el-main>Main</el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  data () {
    return{
       aa: '22' 
    }
  },
  methods: {
    to_xiaonini () {
      this.$router.push({name: 'xiaonini'})
    }
  }
}
</script>

<style>

