import Vue from 'vue'
import Router from 'vue-router'
import wenduidui from '@/views/wenduidui'
import xiaonini from '@/views/xiaonini'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: wenduidui
    },
    {
      path: '/xiaonini',
      name: 'xiaonini',
      component: xiaonini
    }
  ]
})



